import { Component, OnInit } from '@angular/core';
import { GalleryService } from './gallery.service';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
  images = this.galleryService.getAwardsImages();
  activeFilter = 'showall';
  filters = this.galleryService.getFilters();
  constructor(
    private galleryService: GalleryService,
    private router: Router
  ) { }

  ngOnInit(): void {
    window.scrollTo(0, 0)
  }

  loadImages(type?:any) {
    let filterData = [];
    if (type && type !== 'showall') {
      this.activeFilter = type;
      let originalImages = this.galleryService.getAwardsImages();
      filterData = originalImages.filter((image) => {
        return image.type == type;
      })
    } else {
      this.activeFilter = 'showall';
      filterData = this.galleryService.getAwardsImages();
    }
    this.images = filterData;
  }
}
