  initLeaderLine() {
    const banner_image = this.document.getElementById("banner_image");
    banner_image.style.display = "none";
    const banner_button = this.document.getElementById("banner_button");
    banner_button.style.display = "none";
    const procedure_section = this.document.getElementById("procedure-section");
    procedure_section.style.display = "none";
    const about_section = this.document.getElementById("about");
    about_section.style.display = "none";
    const testimonial_section = this.document.getElementById("testimonial-section");
    testimonial_section.style.display = "none";
    const testimonial_break_section = this.document.getElementById("testimonial-break-section");
    testimonial_break_section.style.display = "none";
    const banner_section = this.document.getElementById("banner_section");
    banner_section.style.display = "none";
    document.documentElement.scrollTop = 0;
    setTimeout(() => {

      if (this.genderSelected == 1) {
        const header_text = this.document.getElementById("display-gender-heading-name");
        header_text.style.display = "block";

        const header_backbtn = this.document.getElementById("display-back-Bottoon");
        header_backbtn.style.display = "block";
        header_text.innerHTML = "Male Procedures";
        this.leaderLine.Male.forEach(element => {
          element.start = this.document.getElementById(element.id + "_start");//.filter(bodypart => bodypart.sid === "1")
          element.end = this.document.getElementsByClassName(element.id + "_end");//.filter( ()  =>  $(this).css("display") != "none" )[0];//this.document.getElementsByClassName(element.id + "_end");
          var position = element.end.length <= 1 || element.end.length == undefined ? 0 : element.end[0].style.display != "none" ? element.end[0].id : element.end[1].style.display != "none" ? element.end[1].id : 0;
          // alert(position);
          element.line = new LeaderLine(
            LeaderLine.mouseHoverAnchor(element.start, 'draw', {
              style: { backgroundImage: null, backgroundColor: null }
            }),
            LeaderLine.mouseHoverAnchor(element.end.length == 0 || element.end.length == undefined ? element.end : element.end[0].style.display != "none" ? element.end[0] : element.end[1].style.display != "none" ? element.end[1] : element.end,
              'draw',
              {
                style: { backgroundImage: null, backgroundColor: null },
                x: position == element.id ? element.x1 + '%' : element.x2 + '%',
                y: position == element.id ? element.y1 + '%' : element.y2 + '%',
                width: '20%',
                height: 0,
              }), {
            hide: true,
            color: '#f7f5ee',
            size: 1,
            path: 'straight',
            startPlug: 'disc',
            endPlug: 'disc'
          }
          );
        });

      }
      else if (this.genderSelected == 2) {
        const header_text = this.document.getElementById("display-gender-heading-name");
        header_text.style.display = "block";

        const header_backbtn = this.document.getElementById("display-back-Bottoon");
        header_backbtn.style.display = "block";
        header_text.innerHTML = "Female Procedures";
        this.leaderLine.Female.forEach(element => {
          element.start = this.document.getElementById(element.id + "_start");
          element.end = this.document.getElementsByClassName(element.id + "_end");
          var position = element.end.length <= 1 || element.end.length == undefined ? 0 : element.end[0].style.display != "none" ? element.end[0].id : element.end[1].style.display != "none" ? element.end[1].id : 0;
          // alert(position);
          element.line = new LeaderLine(
            LeaderLine.mouseHoverAnchor(element.start, 'draw', {
              style: { backgroundImage: null, backgroundColor: null }
            }),
            LeaderLine.mouseHoverAnchor(element.end.length == 0 || element.end.length == undefined ? element.end : element.end[0].style.display != "none" ? element.end[0] : element.end[1].style.display != "none" ? element.end[1] : element.end,
              'draw',
              {
                style: { backgroundImage: null, backgroundColor: null },
                x: position == element.id ? element.x1 + '%' : element.x2 + '%',
                y: position == element.id ? element.y1 + '%' : element.y2 + '%',
                width: '20%',
                height: 0,
              }), {
            hide: true,
            color: '#f7f5ee',
            size: 1,
            path: 'straight',
            startPlug: 'disc',
            endPlug: 'disc'
          }
          );
        });
      }
    }, 1000);

  }

