import { Component, OnInit, ViewChild, Inject, ViewEncapsulation } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable, from } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { MatSidenav } from '@angular/material/sidenav';
import { Router } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { TooltipPosition } from '@angular/material/tooltip';

@Component({
  selector: 'app-side-nav',
  templateUrl: './side-nav.component.html',
  styleUrls: ['./side-nav.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class SideNavComponent {

  constructor(@Inject(DOCUMENT) private _document: Document, private toastr: ToastrService, private _router: Router, ) { }

  isMenuOpen = false;
  contentMargin = 60;

  task: string[] = [
    'Clearning out my closet', 'Take out trash bins', 'Wash car', 'Tank up the motorcycles', 'Go for flight training'
  ]
  onToolbarMenuToggle() {
    console.log('On toolbar toggled', this.isMenuOpen);
    this.isMenuOpen = !this.isMenuOpen;

    if (!this.isMenuOpen) {
      this.contentMargin = 0;
    } else {
      this.contentMargin = 60;
    }
    $('mat-sidenav').width(this.contentMargin);
  }
  showToaster() {
    this.toastr.success("Coming Soon!");
  }
  reset() {
    if (this._router.url === '/') {
      this._document.defaultView.location.reload();
    } else {
      this._document.defaultView.location.href = '/';
      // this._router.navigate(['']);
    }
    // this._router.routeReuseStrategy.shouldReuseRoute = () => false;
    // this._router.onSameUrlNavigation = 'reload';
    // this._router.navigate(['/home']);
  }
}