import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { TestimonialsComponent } from './testimonials/testimonials.component';

import { DermatologicalComponent } from './dermatological/dermatological.component';
import { LasersComponent } from './lasers/lasers.component';
import { BotoxandfillersComponent } from './botoxandfillers/botoxandfillers.component';
import { AwardsComponent } from './awards/awards.component';
import { GalleryComponent } from './gallery/gallery.component';




const routes: Routes = [
  { path: '', component: HomeComponent, data: { title: 'Home', genderId: '0' } },
  { path: 'about', component: AboutComponent, data: { title: 'About' } },
  { path: 'contact', component: ContactComponent, data: { title: 'Contact' } },
  { path: 'testimonials', component: TestimonialsComponent, data: { title: 'Testimonials' } },
  { path: 'awards', component: AwardsComponent, data: { title: 'Awards & Recognitions' } },
  { path: 'gallery', component: GalleryComponent, data: { title: 'Gallery' } },
  { path: 'dermatological', component: DermatologicalComponent, data: { title: 'Dermatological' } },
  { path: 'lasers', component: LasersComponent, data: { title: 'Lasers' } },
  { path: 'botoxandfillers', component: BotoxandfillersComponent, data: { title: 'Botox and Fillers' } },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    onSameUrlNavigation: 'reload',
    useHash: false,
    relativeLinkResolution: 'legacy'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule {
  // routes  = [];
}
