import { Component, OnInit } from '@angular/core';
import { AwardsService } from './awards.service';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-awards',
  templateUrl: './awards.component.html',
  styleUrls: ['./awards.component.css']
})
export class AwardsComponent implements OnInit {
  images = this.awardsService.getAwardsImages();
  activeFilter = 'showall';
  constructor(
    private awardsService: AwardsService,
    private router: Router
  ) { }

  ngOnInit(): void {
    window.scrollTo(0, 0)
  }

  loadImages(type?:any) {
    let filterData = [];
    if (type) {
      this.activeFilter = type;
      let originalImages = this.awardsService.getAwardsImages();
      filterData = originalImages.filter((image) => {
        return image.type == type;
      })
    } else {
      this.activeFilter = 'showall';
      filterData = this.awardsService.getAwardsImages();
    }
    this.images = filterData;
  }
}
